.button {
  @extend %flex-center;
  align-items: center;
  text-decoration: none;
  color: $lightGray;
  background-color: $black;
  transition: all 0.3s;
  font-size: 16px;
  line-height: 1.3em;
  letter-spacing: 0.15em;
  text-transform: uppercase;

  &:hover {
    background-color: $beige;
    color: $black;
    border: 1px solid $black;
    box-shadow: 0 13px 27px rgba(120, 120, 120, 0.5);
  }

  &:active {
    transform: scale(0.9);
  }
}
